.new-companys {
	padding-top: 20px;
	&__head {
		text-align: center;
		margin-bottom: 20px; }
	&__title {
		margin-bottom: 10px; }
	&__footer {
		padding-bottom: 20px;
		.row {
			display: flex;
			align-items: center;
			justify-content: center;
			@include xs {
				flex-direction: column; }
			.details__save {
				&:first-child {
					.details__save-btn {
						width: 260px;
						margin-right: 30px;
						@include sm {
							width: 210px; }
						@include xs {
							margin-right: 0;
							margin-bottom: 10px; } } } } } }
	.select {
		width: 100%;
		max-width: 570px;
		@include md {
			max-width: 100%; } }
	.post-form {
		margin-top: 20px;
		margin-left: 250px;
		margin-right: 65px;
		@include md {
			margin-right: 60px; }
		@include sm {
			margin-left: 0; }
		&__title-row {
			display: block;
			position: absolute;
			left: -250px;
			top: 15px;
			padding-right: 32px;
			text-align: right;
			width: 250px;
			@include sm {
				top: -24px;
				left: 0;
				width: auto;
				text-align: left; } }
		&__col {
			width: 100%;
			@include sm {
				margin-top: 30px;
				margin-bottom: 30px; } }
		&__input {
			max-width: 570px;
			margin-bottom: 10px;
			padding: 15px 27px;
			@include md {
				max-width: 100%; }
			@include sm {
				margin-bottom: 10px; } }
		&__label {
			left: 27px;
			@include sm {
				top: 13px;
				left: 27px;
				font-size: 14px; } } } }

.company-form {
	&__row {
		border-bottom: 1px solid #eee;
		margin-bottom: 25px;
		padding-bottom: 25px; }
	.select-wrap {
		position: relative;
		max-width: 100%; }
	.select .select2-selection--single .select2-selection__rendered {
		padding-left: 27px;
		padding-top: 6px;
		padding-bottom: 6px; }
	.select .select2-selection--single {
		height: 45px; }
	.select .select2-selection--single .select2-selection__arrow {
		height: 47px;
		width: 40px; }
	.select2-container--default .select2-selection--multiple,
	.select2-container--default .select2-selection--single {
		border-radius: 0; } }

.file-upload {
	max-width: 175px;
	text-align: center;
	@include sm {
		margin: 0 auto; }
	&__wrap {
		position: relative;
		width: 135px;
		height: 135px;
		border-radius: 50%;
		margin: 0 auto 3px;
		background-color: #e6ddd0;
		padding: 23px;
		z-index: 1; }
	&__photo {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		width: 135px;
		height: 135px;
		border-radius: 50%;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: 50% 50%;
		z-index: 2;
		display: none; }
	&__icon {
		font-size: 34px;
		display: block;
		margin-bottom: 5px;
		.icon {
			fill: $brown; } }
	&__description {
		font-size: 14px;
		color: $brown; }
	&__title-input {
		display: inline-block;
		margin-bottom: 10px;
		color: #c8bdad;
		font-size: 14px;
		&.is-hover {
			opacity: 0; } }
	&__label {
		white-space: nowrap;
		font-size: 15px;
		font-weight: 600;
		@include sm {
			margin-top: 10px; } }
	&__input {
		display: none; }
	input[type="file"] {
		display: none; } }

.hints {
	position: absolute;
	right: -60px;
	top: 0;
	width: 28px;
	height: 28px;
	@include md {
		right: -40px; }
	&__icon {
		border-radius: 50%;
		line-height: 28px;
		text-align: center;
		vertical-align: middle;
		color: $brown;
		background-color: $white_brown_bg;
		transition: color .3s, background-color .3s;
		&:hover {
			color: $white_brown_bg;
			background-color: $brown; } }
	&:hover {
		.hints__text {
			display: block;
			opacity: 1; } }
	&__text {
		position: absolute;
		top: 30px;
		left: -186px;
		width: 200px;
		background-color: $white_brown_bg;
		z-index: 2;
		display: none;
		opacity: 0;
		font-size: 12px;
		color: $brown;
		transition: opacity .5s;
		padding: 15px 10px;
		box-shadow: 0 0 10px rgba(#625a4f,.2); } }

.basic-services {
	.post-form {
		margin-top: 0; }
	.select-wrap {
		max-width: 570px;
		margin-left: 220px;
		margin-right: 60px;
		@include md {
			max-width: 100%; }
		@include sm {
			margin-top: 30px;
			margin-left: 0; } } }

.basic-contacts .post-form,
.basic-services .select-wrap {
	@include md {
		max-width: 100%; } }

.title-box {
	display: flex;
	align-items: center;
	margin-bottom: 20px;
	@include sm {
		flex-direction: column;
		align-items: flex-start; }
	&__title {
		@include sm {
			font-size: 24px !important;
			margin-bottom: 10px; } }
	&__btn {
		margin-left: 58px;
		@include sm {
			margin-bottom: 20px;
			margin-left: 0; } } }


.basic-contacts {
	&__map {
		margin-bottom: 38px; }
	.post-form {
		margin-top: 0;
		max-width: 570px;
		margin-left: 220px;
		margin-bottom: 25px; }
	.map {
		height: 410px; }
	.map-wrap {
		padding: 0;
		margin-bottom: 38px; } }

.company-form__row_mod {
	.select-wrap {
		padding-bottom: 60px; }
	.basic-contacts.is-active {
		.company-form__row_mod {
			padding-bottom: 60px; } } }

.sol-caret-container .sol-caret {
	left: 0;
	top: 5px;
	border-top: 4px solid #888;
	border-right: 4px solid transparent;
	border-left: 4px solid transparent; }

.sol-inner-container {
	height: 45px;
	background-color: #efece6;
	border-radius: 0;
	border: none; }

.sol-input-container input[type=text] {
	color: #62594f;
	padding-left: 27px; }

.sol-results-count,
.sol-selected-display-item {
	display: inline-table;
	border: 1px solid #c7bfb7;
	background: #ffffff;
	font-size: .9em;
	margin-right: 5px;
	margin-bottom: 5px;
	border-collapse: separate;
	border-radius: 4px; }

.sol-quick-delete {
	color: #65594d; }

.sol-current-selection {
	position: absolute;
	top: 50px;
	left: 0; }

.contact-form {
	&__textarea {
		.post-form {
			max-width: 100%; } }
	.post-form {
		margin-right: 0;
		margin-bottom: 0;
		@include md {
			margin-right: 0; }
		&__input {
			max-width: 370px; }
		&__textarea {
			height: 76px;
			margin-bottom: 5px;
			@include sm {
				margin-bottom: 0; } } }
	.post-form__col {
		@include sm {
			margin-bottom: 0; } }
	.post-form__mods {
		margin-left: 220px;
		&__input {
			max-width: 570px; } } }

.upload-photo {
	&__gallery {
		margin: 0 15px; }
	&__item {
		position: relative;
		width: 272px;
		height: 175px;
		background-color: #e6ddd0;
		padding: 50px 23px 23px;
		z-index: 1;
		text-align: center;
		margin-bottom: 30px;
		@include sm {
			width: 100%; }
		&:not(:last-child) {
			margin-right: 30px; } }
	&__photo {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		width: 100%;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: 50% 50%;
		z-index: 2; }
	&__icon {
		font-size: 34px;
		display: block;
		margin-bottom: 5px;
		.icon {
			fill: $brown; } }
	&__description {
		font-size: 14px;
		color: $brown; }
	.select-wrap {
		margin-bottom: 10px; } }

.upload-gallery {
	.post-form {
		margin: 0; }
	.title-hint {
		display: block;
		margin-bottom: 15px;
		font-size: 15px;
		color: #65594d; }
	.file-upload {
		display: flex;
		align-items: center;
		max-width: 100%;
		@include sm {
			flex-direction: column;
			align-items: flex-start; }
		.post-form__input {
			@include sm {
				margin-bottom: 0; } } }
	.post-form__col {
		max-width: 470px;
		margin-right: 32px;
		@include sm {
			margin-top: 0;
			margin-bottom: 0; }
		.post-form__icon {
			position: absolute;
			top: 50%;
			right: 25px;
			transform: translate(50%, -50%);
			color: $brown;
			font-size: 16px; } }
	.post-form__input {
		max-width: 100%;
		margin-bottom: 0;
		padding-right: 45px; } }

.table {
	&__thead {
		padding-bottom: 8px;
		margin-bottom: 8px;
		border-bottom: 2px solid $brown;
		margin-right: 35px; }
	&__tbody {
		max-height: 460px;
		overflow: auto;
		.table__tr {
			padding: 4px 0;
			margin-bottom: 4px;
			margin-right: 35px;
			&:not(:last-child) {
				border-bottom: 1px solid #eee; } } }
	&__tr {
		display: flex;
		align-items: flex-start;
		@include sm {
			flex-direction: column; } }
	&__th {
		font-size: 14px;
		font-weight: 600;
		color: $brown;
		text-align: left;
		flex: 1 1 25%;
		@include sm {
			flex: 1 1 100%; } }
	&__td {
		width: 100%;
		text-align: left;
		&:not(:last-child) {
			margin-right: 27px;
			text-align: center;
			@include sm {
				margin-right: 0; } }
		&:not(:first-child) {
			padding: 12px 0; }
		&:last-child {
			margin-right: 35px; } }
	&__img {
		width: 100%;
		height: 100px;
		font-size: 0;
		@include sm {
			height: 150px; }
		img {
			max-width: 100%;
			height: 100%; } }
	&__name {
		color: $brown;
		font-size: 14px; }
	&__block-btn {
		padding-top: 12px;
		width: 100%;
		text-align: right;
		white-space: nowrap;
		order: -1;
		@include sm {
			margin-bottom: 10px; }
		a {
			border-bottom: none !important; } }
	&__btn {
		background: none;
		border: none;
		outline: none;
		font-size: 20px;
		.icon {
			fill: $brown; }
		&:not(:last-child) {
			margin-right: 25px; } } }

#style-scroll::-webkit-scrollbar-track {
	// -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #Ffffff;
	border: 1px solid #c4c1c1; }

#style-scroll::-webkit-scrollbar {
	width: 5px;
	background-color: #F5F5F5; }

#style-scroll::-webkit-scrollbar-thumb {
	background-color: #c4c1c1; }


.upload-photo {
	&__nav {
		border-top: 1px solid #eee;
		padding-top: 20px;
		margin-bottom: 36px;
		.page-nav {
			@include xs {
				justify-content: flex-start; } }
		.page-nav__item.active {
			font-weight: 600; } }
	.upload-photo__gallery_mod {
		margin: 0; }
	.post-form {
		margin-top: 0; } }

.sorting {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	&__col {
		display: flex;
		align-items: center;
		&:first-child {
			margin-right: 35px; }
		@include md {
			margin-bottom: 10px; } }
	&__title {
		font-size: 16px;
		color: $brown; }
	&__list {
		position: absolute;
		left: 26px;
		top: 29px;
		display: none;
		background-color: $white;
		box-shadow: 0 0 10px rgba(#625a4f,.2);
		padding: 20px 15px;
		z-index: 5; }
	&__dropdown {
		position: relative;
		&:hover {
			.sorting__list {
				display: block; } } }
	&__active-name {
		display: flex;
		align-items: center;
		margin-left: 26px;
		font-size: 15px;
		color: #c4c1c1;
		padding-bottom: 1px;
		border-bottom: 2px solid #c4c1c1;
		cursor: pointer; }
	&__icon {
		padding-top: 5px;
		margin-left: 15px;
		.icon {
			fill: #c4c1c1; } }
	&__item {
		&:not(:last-child) {
			border-bottom: 1px solid #eee; } }
	&__link {
		display: block;
		padding: 5px;
		color: #c4c1c1;
		transition: color .3s;
		&:hover {
			color: $brown; } } }





.counter-price {
	display: flex;
	align-items: center;
	justify-content: center;

	&__input {
		width: 34px; }
	&__box-btn {
		display: flex;
		flex-direction: column; }
	&__plus {
		margin-bottom: 10px;
		transform: rotate(180deg); }
	&__plus,
	&__minus {
		background: none;
		border: none;
		outline: none;
		height: 16px;
		.icon {
			fill: $brown;
			font-size: 16px; } } }

.add-product {
	text-align: center;
	margin-bottom: 60px;
	&__btn {
		display: block;
		width: 48px;
		height: 48px;
		border-radius: 50%;
		background-color: $white_brown_bg;
		line-height: 44px;
		vertical-align: middle;
		text-align: center;
		font-size: 28px;
		color: $brown;
		margin: 0 auto;
		transition: color .3s, background-color .3s;
		&:hover {
			color: $white_brown_bg;
			background-color: $brown; } }
	&__text {
		margin-top: 10px;
		color: $brown;
		display: inline-block; } }


.details {
	&__save {
		text-align: center; }
	&__save-btn {
		font-weight: 600; }
	.contact-form__textarea {
		margin-bottom: 50px; } }

