$icons: (0:0);
$icons: map-merge($icons,('facebook': (X: -213px, Y:-96px, W: 10px, H: 19px, TW: 239px, TH: 127px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('facebook@2x': (X: -80px, Y:-57px, W: 20px, H: 38px, TW: 239px, TH: 127px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('icon8-forward': (X: -177px, Y:0px, W: 26px, H: 86px, TW: 239px, TH: 127px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('icon8-prev': (X: -213px, Y:0px, W: 26px, H: 86px, TW: 239px, TH: 127px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('sberbank': (X: 0px, Y:0px, W: 167px, H: 47px, TW: 239px, TH: 127px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('send-button': (X: -110px, Y:-57px, W: 27px, H: 26px, TW: 239px, TH: 127px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('yandex-pay': (X: 0px, Y:-57px, W: 70px, H: 70px, TW: 239px, TH: 127px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y);
  display: inline-block; }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: floor($height/2);
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i);
  display: inline-block; }

