.memory-page {
    background-color: $white_brown_bg;
    padding: 60px;
    &__container {}
    &__head {
        margin-bottom: 35px; }
    &__title {
        line-height: 0.9;
        margin-bottom: 17px; }
    &__text {
        max-width: 775px;
        margin: 0 auto;
        margin-bottom: 50px;
        p {
            line-height: 1.6;
            &:not(:last-child) {
                margin-bottom: 23px; } } }
    &__slider {
        margin-bottom: 40px; }
    &__item {
        padding: 19px 1px 19px 11px;
        margin: 7px; } }


.memory-slider {
    padding: 0 25px;
    &__prev,
    &__next {
        position: absolute;
        color: $brown_orange;
        font-size: 20px;
        font-weight: 300;
        background-color: transparent;
        top: 50%;
        transform: translateY(-50%); }
    &__next {
        right: 0; }
    &__prev {
        left: 0; }
    .memory-item {
        display: flex;
        justify-content: center; } }

.memory-item {
    display: flex;
    padding: 26px 8px 26px 18px;
    @include sm {
        flex-direction: column;
        align-items: center; }
    &:hover {
        background-color: #e8e3dc;
        box-shadow: 1px 1px 9px 1px rgba(0,0,0,.27); }
    &__person {
        display: flex;
        flex-direction: column;
        margin-right: 18px; }
    &__photo {
        position: relative;
        width: 120px;
        height: 105px;
        img {
            display: block;
            width: 100%; } }
    &__img {
        position: absolute;
        width: 38px;
        height: 35px;
        left: 0;
        bottom: 0;
        img {
            display: block;
            width: 100%; }
        &_1 {
            width: 57px;
            height: 28px;
            left: auto;
            right: 0; } }
    &__general {
        padding-top: 9px;
        font-family: $cormorant_infant;
        font-weight: 600;
        span {
            font-family: $tahoma;
            font-size: 9px;
            font-weight: normal; } }
    &__details {
        padding-top: 15px;
        font-size: 14px;
        span {
            font-weight: 700; } } }

.underlined {
    text-decoration: underline; }

.flaticon-null {
    font-family: Flaticon; }
