.error-content {
	position: relative;
	&__text {
		position: absolute;
		top: 74%;
		left: 58%;
		transform: translate(-50%, -50%);
		text-align: center;
		font-family: $tahoma;
		@include md {
			top: 82%;
			left: 53%; }
		@include sm {
			top: 78%; }
		h3 {
			font-size: 22px;
			line-height: 1.3;
			color: $white;
			white-space: nowrap;
			@include md {
				font-family: $cormorant_infant;
				font-size: 18px; }
			@include sm {
				margin-bottom: 0px; } }
		p {
			display: block;
			line-height: 1.3;
			font-size: 16px;
			color: $brown_orange;
			margin-bottom: 30px;
			@include md {
				font-size: 12px;
				margin-bottom: 18px; } }
		a.btn {
			font-size: 22px;
			@include md {
				font-size: 16px; }
			@include sm {
				min-width: 152px;
				font-size: 12px;
				padding: 5px 10px; } } }

	.error-head {
		max-width: 100%;
		height: 0;
		padding-bottom: 50%;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: 50% 50%;
		margin-bottom: 0;
		position: relative;
		margin-top: -2px;
		min-height: 320px; } }
