.order-flowers {
    padding: 50px 0 30%;
    background-color: $white_brown_bg;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50% 100%;
    margin-bottom: 65px;
    &__title {
        margin-bottom: 35px; }
    &__head {
        max-width: 1030px;
        margin: 0 auto;
        margin-bottom: 50px; }
    a {
        position: relative;
        &:not(:last-child) {
            margin-right: 60px; }
        span {
            position: absolute;
            width: 55px;
            height: 100%;
            background-color: #fff;
            left: -20px;
            top: 0;
            border-radius: 5px 0 0 5px;
            font-size: 30px;
            color: $brown_orange;
            display: flex;
            justify-content: center;
            align-items: center; } } }
